import { makeStyles } from "@material-ui/styles";
import FileCopyIcon from "@material-ui/icons/FileCopy";
const useStyle = makeStyles((theme) => ({
  root: {
    [theme.breakpoints.down("md")]: {
      width: "100%",
      marginBottom: "450px",
    },
    margin: "auto auto 300px auto",
    display: "flex",
    flexWrap: "wrap",
    width: "60%",
    gap: ".5rem",
  },

  form: {
    width: "100%",
    display: "flex",
    gap: ".5rem",
    "& > *": {
      fontFamily: "Nunito",
      border: "none",
      padding: ".5rem 1rem",
      borderRadius: "4px",
    },
  },
  input: {
    width: "100%",
    outline: "none",
  },

  button: {
    cursor: "pointer",
    background: "#ff5200",
    color: "#ffffff",
    fontSize: "1rem",
    letterSpacing: "1px",
    fontWeight: "600",
  },

  outputURL: {
    width: "100%",
    padding: ".5rem 1rem",
    backgroundColor: "#2d2d2d",
    borderRadius: "4px",
    color: "#bbbbbb",
    display: "flex",
    alignItems: "center",
    justifyContent: "space-between",
  },
  copyBotton: {
    cursor: "pointer",
    background: "none",
    border: "none",
    color: "#bbbbbb",
    padding: "0",
    "&:hover": {
      color: "white",
    },
  },
}));

const Form = ({ inputUrl, outputUrl, handleInputChange, submitUrl }) => {
  const classes = useStyle();
  return (
    <section className={classes.root}>
      <form className={classes.form} onSubmit={submitUrl}>
        <input
          value={inputUrl}
          onChange={handleInputChange}
          className={classes.input}
          type="text"
        />
        <button className={classes.button}>Shorten</button>
      </form>

      {outputUrl && (
        <div className={classes.outputURL}>
          {outputUrl}
          <button title="copy url" className={classes.copyBotton}>
            <FileCopyIcon
              onClick={() => {
                navigator.clipboard.writeText(outputUrl);
              }}
            ></FileCopyIcon>
          </button>
        </div>
      )}
    </section>
  );
};

export default Form;

